<template>
  <div id='hangzhou-page'>
    <el-row :span="8">
      <el-card shadow="always" class = "first-card">
        <div>
          <div class="date">{{nowDate}}</div>
          <div class="time">{{nowTime}}</div>
        </div>
        <div>
          <el-divider></el-divider>
        </div>

        <div id="name-box">
          <span class="name">{{name}} </span><el-button type="text" class="show" @click="handleName">{{handleNameBtnText}}</el-button>
        </div>
        <el-image
          class="scanned"
          style="width: 20%; height: 20%; float: right;"
          :src="require('@/assets/scanned.jpg')"
          :fit="fit">
        </el-image>
        <div id="qrc-box">
          <div class="address" @click="dialogVisible = true">
            {{address}}
          </div>
          <el-dialog :visible.sync="dialogVisible" :append-to-body="true" width="80%">
            <el-form :model="addressForm" status-icon ref="registerForm" label-width="80px">
              <el-form-item label="地址" prop="address">
                <el-autocomplete
                  class="inline-input"
                  v-model="addressForm.address"
                  :fetch-suggestions="querySearch"
                  placeholder="请输入地址"
                  :trigger-on-focus="false"
                ></el-autocomplete>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('addressForm')">提&emsp;交</el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
          <el-image
            style="width: 60%; height: 60%; margin-top: 5px;"
            :src="require('@/assets/qrc.png')"
            :fit="fit">
          </el-image>
          <div class="health-code-name">杭州健康码</div>
          <div class="vaccinum">已完成新冠疫苗加强接种</div>
        </div>
      </el-card>
    </el-row>
    <el-row :span="8">
      <el-card shadow="always" class = "second-card">
        <el-card shadow="never" class="nucleic-acid">
          <el-row>
            <el-col :span="8">
              <span style="font-size: 45px;">48</span>
            </el-col>
            <el-col :span="16" style="padding-left: 20px">
              <span style="font-size: 15px;">
                小时内<br/>
                <span style="font-size: 20px;">阴性</span>
              </span>
            </el-col>
          </el-row>
          <el-row>
            <el-button round  class= "nucleic-acid-btn">
              核酸检测
              <span class="nucleic-acid-icon">></span>
            </el-button>
          </el-row>
        </el-card>
<!--        <el-card style="width: 2px">-->
<!--          <el-divider direction="vertical" style="height: 100px; width: 1px"></el-divider>-->
<!--        </el-card>-->
        <div class="divider"></div>

        <el-card shadow="never" class="key-areas" >
          <el-row>
            <el-col :span="11">
              <el-image
                style="width: 100%; height: 100%; "
                :src="require('@/assets/key-areas.jpg')"
                :fit="fit">
              </el-image>
            </el-col>
            <el-col :span="13" style="margin-top: 10px;">
            <span style="font-size: 15px;">
             疫情重点<br/>地区核验
            </span>
            </el-col>
          </el-row>
          <el-row style="font-size: 12px; color: #8a8a8a; margin-bottom: -5px">
            <span style="font-size: 12px; color: #8a8a8a;">
              您于7天内未到访
            </span>
          </el-row>
          <el-row style="margin-top: -5px">
            <span style="font-size: 12px; color: #8a8a8a;">
              疫情重点地区所在市
            </span>
          </el-row>
        </el-card>
      </el-card>
      <el-card shadow="always" class = "third-card">
        <el-col :span="16" style="text-align: left;">
          <el-row >
            <el-col :span="4">
              <el-image
                style="width: 100%; height: 100%;"
                :src="require('@/assets/trip.jpg')"
                :fit="fit">
              </el-image>
            </el-col>
            <el-col :span="20">
              <span style="font-size: 15px; margin-left: 5px">行程卡</span>
            </el-col>
          </el-row>
          <el-row>
            <span style="font-size: 10px; color: #8a8a8a; margin-left: 2px; line-height: 0.5em;">
              查看7天是否到过中高风险地区
            </span>
          </el-row>
        </el-col>
        <el-col :span="8">
          <el-button round class="trip-btn" @click="jumpToTripCard">详情</el-button>
        </el-col>
      </el-card>
      <el-image
        style="width: 50%; height: 50%; margin-top: 5px"
        :src="require('@/assets/footer.jpg')"
        :fit="fit">
      </el-image>
    </el-row>
  </div>
</template>

<script>
    export default {
        name: "home",
        data () {
            return{
                nowDate: "",//当前日期
                nowTime: "",//当前时间
                address: "浙商银行股份有限公司",
                dialogVisible: false,
                addressForm: {
                    address: ''
                },
                addressList: [],
                name: "张*",
                handleNameBtnText: "显示"
            }
        },
        methods:{
            currentTime() {
                setInterval(this.formatDate, 500);
            },
            formatDate() {
                let date = new Date();
                let month = date.getMonth() + 1; // 月
                let day = date.getDate(); // 日
                day = day < 10 ? "0" + day : day; // 如果只有一位，则前面补零
                let hour = date.getHours(); // 时
                hour = hour < 10 ? "0" + hour : hour; // 如果只有一位，则前面补零
                let minute = date.getMinutes(); // 分
                minute = minute < 10 ? "0" + minute : minute; // 如果只有一位，则前面补零
                let second = date.getSeconds(); // 秒
                second = second < 10 ? "0" + second : second; // 如果只有一位，则前面补零
                this.nowDate = `${month}月${day}日`;
                this.nowTime = `${hour}:${minute}:${second}`;
            },
            submitForm (formName) {
                if (formName === 'addressForm') {
                    this.address = this.addressForm.address;
                    this.dialogVisible = false;
                }
            },
            querySearch(queryString, cb) {
                var addressList = this.addressList;
                var results = queryString ? addressList.filter(this.createFilter(queryString)) : addressList;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (addressList) => {
                    return (addressList.address.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            loadAll() {
                return [
                    { "address": "浙商银行股份有限公司" },
                    { "address": "立涛园社区人才公寓" }
                ];
            },
            handleName(){
                if (this.handleNameBtnText === "显示") {
                    this.handleNameBtnText = "隐藏";
                    this.name = "张磊";
                } else {
                    this.handleNameBtnText = "显示";
                    this.name = "张*";
                }
            },
            jumpToTripCard() {
                window.location.href = "https://ur.alipay.com/5Sp5mX06aCdtMkuC0fsIbQ";
            }
        },
        mounted() {
            this.currentTime();
            this.addressList = this.loadAll();
        },
        // 销毁定时器
        beforeDestroy() {
            if (this.formatDate) {
                clearInterval(this.formatDate); // 在Vue实例销毁前，清除时间定时器
            }
        }
    }
</script>

<style scoped>
  #hangzhou-page{
    width:100%;
    height:100%;
    position:fixed;
    background-size:cover;
    background: linear-gradient(
      to bottom,
      #54a668,
      #54a668 20%,
      white 20%,
      white 100%
    );
  }
  >>>.el-card__body{
    padding: 0;
  }
  .first-card{
    border-radius: 10px;
    margin: 18px 20px 5px 20px;
    padding: 20px 10px 0px 10px;
  }
  .second-card{
    border-radius: 10px;
    margin: 5px 20px 5px 20px;
    padding: 15px 20px;
  }
  .third-card{
    border-radius: 10px;
    margin: 10px 20px 5px 20px;
    padding: 15px 20px;
  }
  .el-divider{
    margin: 8px 0;
    background: 0 0;
    border-top: 1px solid #E6EBF5;
  }
  .date{
    transform: scale(1, 1.05);
    /*font-family: "Microsoft YaHei";*/
    font-size: 40px;
    font-weight: bold;
    color: #46a761;
    letter-spacing: 1px;
  }
  .time{
    /*font-family: "Microsoft YaHei";*/
    font-size: 55px ;
    font-weight: bold;
    color: #46a761;
  }
  #name-box{
    margin-top: -10px;
    margin-bottom: -5px;
    /*text-align:left;*/
    float: left;
  }
  #qrc-box{
    float:left;
    position:relative;
    margin-top: -45px;
  }
  .name{
    font-weight: bold;
    font-size: 18px;
  }
  .show{
    margin-left: 10px;
    font-size: 12px;
  }
  .address{
    margin-top: 5px;
    font-weight: bold;
    font-size: 18px;
  }
  .health-code-name{
    font-weight: bold;
    font-size: 18px;
  }
  .vaccinum{
    color: #c97c13;
    margin-top: 3px;
    margin-bottom: 10px;
  }
  .nucleic-acid {
    width: 45%;
    height: 120px;
    background: linear-gradient(to top right, #539de4 0%, #61aec2 50%, #68cc9a 100%);
    color: white;
    float: left;
    display:flex;
    justify-content: center;
    align-items: center;
  }
  .divider{
    float:left;
    position:relative;
    margin-left: 7.5%;
    height:120px;
    width:0px;
    border-left:1px solid #DCDFE6
  }
  .key-areas{
    width: 45%;
    height: 120px;
    float: right;
  }
  .el-card{
    border: none;
  }
  .nucleic-acid-btn{
    background-color: rgba(0, 0, 0, 0.0);
    color: white;
    padding: 5px 10px 5px 10px;
    font-size: 10px;
    letter-spacing: 1px;
    margin-top: 10px;
  }
  .nucleic-acid-icon{
    border-radius: 50%;
    background-color: white;
    color: #a4a3b7;
    padding: 0 2px;
  }
  .trip-btn{
    color: #4f8fe1;
    border: 1px solid #4f8fe1;
    padding: 5px 20px 5px 20px;
    margin: 10%;
  }
</style>


